
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
}
*{
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #555d6f;
}

#header {
  padding-top: 10px;
  height: 100%;
  background-color: #555d6f;
  text-align: center;
}
#avatar{
  background-color: #555d6f;
  height: auto;
  padding-bottom: 10px;
}
#avatar img {
  width: 70px;
  height: 70px;
  border: 3px solid #868686;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 35px;
}

#bar{
  height: auto;
}
span {
  background-color: #ffffff;
}
#bar ul{
  text-align: center;
}
#bar li{
  display: inline;
}

.article {
  width: 100%;
  margin: 0;
  padding: 0;
  border: none;
  background-color: #f0f0f0;
  border-radius: 0 0 5px 5px;
}
